import React, { useState } from "react";
import classNames from "classnames";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import { TooltipSvg } from "../TooltipSvg";
import { Input } from "@/components";
import "./styles.scss";


const ContractCardItem = ({ label, value, style = {} }) => {
  return (
    <div className="contract-card-item" style={style}>
      <div className="contract-card-item__label">{label}</div>
      <div className="contract-card-item__value">{value}</div>
    </div>
  );
};

export default function ContractCards(props)  {
  return (
    <div className="contract-cards">
      {props.tableData.map((row, idx) => (
        <Card
          className={classNames("contract-card", "contract-cards__item", {
            "contract-card--selected": !!row?.hoosed_item,
          })}
          key={idx}
        >
          <CardContent>
            <div className="contract-card__checkbox checkbox bounce">
              <input
                type="checkbox"
                defaultChecked={!!row?.hoosed_item}
                onChange={() =>
                  props.handleTableChange(
                    idx,
                    "hoosed_item",
                    !row?.hoosed_item
                  )
                }
              />
              <svg viewBox="0 0 21 21">
                <polyline points="5 10.75 8.5 14.25 16 6"></polyline>
              </svg>
            </div>
            <ContractCardItem
              label="  "
              value=" 135  12.12.2020  "
              style={{ width: "calc(100% - 30px)" }}
            />
            <div className="contract-card__row">
              <ContractCardItem
                label=" "
                value="10 000 "
              />
              <ContractCardItem
                label="    "
                value="30 000 "
              />
            </div>
            <div className="contract-card__row">
              <ContractCardItem
                label={
                  <div className="contract-card__tooltip-text">
                    <span>
                      ,
                      <br /> 
                    </span>
                    <TooltipSvg message="         " />
                  </div>
                }
                value={<span className="contract-card__red">1 000 </span>}
              />
              <ContractCardItem
                label={
                  <>
                    
                    <br />  
                  </>
                }
                value="0 "
              />
            </div>

            <ContractCardItem
              label={
                <>
                   
                  <TooltipSvg message="   .         ,    " />
                </>
              }
              value={
                <Input
                  value={row["input_item"]}
                  onChange={(e) =>
                    props.handleTableChange(idx, "input_item", e.target.value)
                  }
                  border
                  disabled={false}
                  style={{ height: "48px", fontSize: "20px" }}
                />
              }
            />
          </CardContent>
        </Card>
      ))}
    </div>
  );
};


