import React, { useState } from "react";
import SVG from "react-inlinesvg";
import { Layout } from "@/views/layout";
import WarningModal from "@/container/warningModal";
import DisabledInput from "@/components/disabledInput/index";
import { Accordion, Input, Button, Modal } from "@/components";
import { history } from "@/helpers";
import TooltipText from "public/tooltipText.svg";
import "./style.scss";

const faqItems = [
  {
    title: "    ?",
    text: "                 ,   ,  ,     ,    . ",
  },
  {
    title:
      "      .     ?",
    text: "           .",
  },
  {
    title: "     .  ?",
    text: "           .    ,   ,        .      ,  .        call-   595.",
  },
  {
    title: "  / ?",
    text: "          ' .'",
  },
  {
    title: "   ? ",
    text: "     22:00    .        16:00.     ,        .",
  },
  {
    title: "    ? ",
    text: "            . ",
  },
  {
    title: "    ?",
    text: "        .    ,    .",
  },
];

const PaymentWithoutSms = (props) => {

  const [isOpen, setIsOpen] = useState(false);
  const [paymentSum, setPaymentSum] = useState("");
  
  const mobile = window.innerWidth < 600;
  return (
    <Layout>
      <div className="payment-without-sms">
      <Modal isOpen={isOpen} handleFunc={() => setIsOpen(false)}>
          <WarningModal />
        </Modal>
        <div className="payment-without-sms__container">
          {!mobile && (
            <>
              <div className="f-c">
                <DisabledInput label="" text="  " />
                <DisabledInput label=" " text="11000 " />
                <div style={{ marginTop: "36px" }}></div>
                <Input
                  border
                  style={{ height: "48px", maxWidth: "325px" }}
                  type="number"
                  label=" "
                  value={paymentSum}
                  onChange={(event) => setPaymentSum(event.target.value)}
                />
              </div>
              <div className="f-c">
                <DisabledInput label="" text="9601233212233" />
                <DisabledInput label=", " text="0 " />
                <div style={{ marginTop: "36px" }}></div>
                <Button
                  className="success p-y-15 m-t-15 m-b-30 m-l-20"
                  label=""
                  onClick={() => {
                    if (+paymentSum<11000) {
                      setIsOpen(true);
                    } else {
                      history.push('/result');
                    }
                  }}
                  disabled={+paymentSum>0? false: true}
                />

                <SVG src={TooltipText} />
              </div>
            </>
          )}

          {mobile && (
            <>
              <div className="f-c">
                <DisabledInput label="" text="  " />
                <DisabledInput label="" text="9601233212233" />
                <DisabledInput label=" " text="11000 " />
                <DisabledInput label=", " text="0 " />
                <div style={{ marginTop: "36px" }}></div>
                <Input
                  border
                  style={{ height: "48px", maxWidth: "325px" }}
                  type="number"
                  label=" "
                  value={paymentSum}
                  onChange={(event) => setPaymentSum(event.target.value)}
                />
              </div>
              <div className="f-c">
                <div style={{ marginTop: "36px" }}></div>
                <Button
                  className="success"
                  style={{ width: "100%" }}
                  label="  "
                  onClick={() => {
                    if (+paymentSum<11000) {
                      setIsOpen(true);
                    } else {
                      history.push('/result');
                    }
                  }}
                  disabled={+paymentSum>0? false: true}
                />
              </div>
            </>
          )}
          <div className="f-c">
            <div className="payment-without-sms__container-box">
              <p className="payment-without-sms__container-item">
                      {" "}
                <strong></strong>
              </p>
              <br />
              <p className="payment-without-sms__container-item">
                   19:00   ,    
                       
              </p>
              <br />
              <p className="payment-without-sms__container-item">
                          
                
              </p>
            </div>
          </div>
        </div>
        <div className="container" style={{ marginTop: "80px" }}>
          <Accordion faqItems={faqItems} />
        </div>
      </div>
    </Layout>
  );
};

export default PaymentWithoutSms;
