import React, { useState } from "react";
import SVG from "react-inlinesvg";
import { Layout } from "@/views/layout";
import DisabledInput from "@/components/disabledInput/index";
import {
  Accordion,
  Tabs,
  Table,
  ContractCards,
  Button,
} from "@/components";
import { history } from "@/helpers";
import TooltipText from "public/tooltipText.svg";
import "./style.scss";

const faqItems = [
  {
    title: "    ?",
    text: "                 ,   ,  ,     ,    . ",
  },
  {
    title:
      "      .     ?",
    text: "           .",
  },
  {
    title: "     .  ?",
    text: "           .    ,   ,        .      ,  .        call-   595.",
  },
  {
    title: "  / ?",
    text: "          ' .'",
  },
  {
    title: "   ? ",
    text: "     22:00    .        16:00.     ,        .",
  },
  {
    title: "    ? ",
    text: "            . ",
  },
  {
    title: "    ?",
    text: "        .    ,    .",
  },
];

const tabTitles = [
  {
    title: " ",
    value: 0,
  },
  {
    title: " ",
    value: 1,
  },
];

const tableTitles = [
  { name: "", field: "hoosed_item", type: "checkbox" },
  { name: "  ", field: "id" },
  {
    name: " ",
    field: "borrower_full_name",
    type: "currency",
  },
  { name: " ", field: "borrower_iin", type: "currency" },
  {
    name: ",",
    field: "fines_payment",
    tooltipText: "         ",
    type: "fines",
  },
  {
    name: " ",
    field: "input_item",
    tooltipText:
      "   .         ,    ",
    type: "input",
  },
  { name: " ", field: "status" },
];

const tableTitles2 = [
  { name: "", field: "hoosed_item", type: "checkbox" },
  { name: "  ", field: "id" },
  {
    name: " ",
    field: "borrower_full_name",
    type: "currency",
  },

  {
    name: ",",
    field: "fines_payment",
    tooltipText: "         ",
    type: "fines",
  },
  {
    name: "    ",
    field: "borrower_full_name",
    type: "currency",
  },
  {
    name: " ",
    field: "input_item",
    tooltipText:
      "   .         ,    ",
    type: "input",
  },
  {
    name: "  ",
    field: "borrower_full_name",
    type: "currency",
  },
];


const PaymentPage = (props) => {

  const [totalForPayment, setTotalForPayment] = React.useState(0);
  const [tableData, setTableData] = useState([
    {
      id: " 135  12.12.2020 ",
      borrower_full_name: "100000",
      borrower_iin: "10000",
      fines_payment: "1000",
      input_item: "11000",
      hoosed_item: false,
    },
    {
      id: " 135  12.12.2020  ",
      borrower_full_name: "120000",
      borrower_iin: "50000",
      fines_payment: "3000",
      input_item: "53000",
      hoosed_item: false,
    },
    {
      id: " 135  12.12.2020 ",
      borrower_full_name: "140000",
      borrower_iin: "10000",
      fines_payment: "2000",
      input_item: "12000",
      hoosed_item: false,
    },
  ]);

  const handleTableChange = (index, field, value) => {
    
    let newTableData = [];
    if (tableData.length > 0) {
      for (let i = 0; i < tableData.length; i++) {
        if (index !== i) {
          newTableData.push(tableData[i]);
        } else {
          newTableData.push({ ...tableData[i], [field]: value });
        }
      }

      let top = 0;
      if (newTableData.length > 0) {
        for (let i = 0; i < newTableData.length; i++) {
          if (
            newTableData[i].hoosed_item &&
            Number(newTableData[i].input_item)
          ) {
            top += Number(newTableData[i].input_item);
          }
        }
      }
      console.log(index,field,value)
      setTotalForPayment(top);
      setTableData(newTableData);
    }
  };

  const mobile = window.innerWidth < 600;
  return (
    <Layout>
      <div className="home">
        {!mobile && (
          <>
            <div className="container" style={{ marginTop: "40px" }}>
              <h3>  </h3>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "space-between",
                  maxWidth: "700px",
                }}
              >
                <DisabledInput label="" text="  " />
                <DisabledInput label="" text="9601233212233" />
              </div>
            </div>
            <div className="container" style={{ marginTop: "80px" }}>
              <Tabs tabTitles={tabTitles}>
                <Table
                  tableTitle={tableTitles}
                  tableData={tableData}
                  page={0}
                  handleTableChange={handleTableChange}
                />

                <Table
                  tableTitle={tableTitles2}
                  tableData={tableData}
                  page={0}
                  handleTableChange={handleTableChange}
                />
              </Tabs>
            </div>{" "}
            <div
              className="container"
              style={{
                marginTop: "80px",
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              <div style={{ display: "flex", flexDirection: "column" }}>
                <h2>  : {totalForPayment} </h2>
                <p style={{ maxWidth: "430px" }}>
                  *    <strong>16:00</strong>   ,
                      <strong>/</strong> 
                     {" "}
                </p>
              </div>
              <div style={{ width: "300px" }}>
                <Button
                  className="success"
                  label="  "
                  onClick={() => history.push('/result')}
                  disabled={totalForPayment <= 0}
                />
                <SVG src={TooltipText} />
              </div>
            </div>
            <div className="container" style={{ marginTop: "80px" }}>
              <Accordion faqItems={faqItems} />
            </div>
          </>
        )}

        {mobile && (
          <>
            <div
              className="container"
              style={{
                marginBottom: "50px",
                marginTop: "36px",
                display: "flex",
                justifyContent: "center",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <h3>  </h3>
                <DisabledInput label="" text="  " />
                <DisabledInput label="" text="9601233212233" />
              </div>
            </div>
            <div className="container">
              <Tabs tabTitles={tabTitles}>
                <ContractCards
                  tableTitle={tableTitles}
                  tableData={tableData}
                  page={0}
                  handleTableChange={handleTableChange}
                />
                <ContractCards
                  tableTitle={tableTitles2}
                  tableData={tableData}
                  page={0}
                  handleTableChange={handleTableChange}
                />
              </Tabs>
            </div>
            <div
              className="container"
              style={{
                marginTop: "50px",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",
                marginBottom: "50px",
              }}
            >
              <h2>  : {totalForPayment} </h2>
              <p style={{ maxWidth: "430px" }}>
                *    <strong>16:00</strong>   , 
                   <strong>/</strong>  
                  {" "}
              </p>

              <Button
                className="success p-y-15 m-t-50 m-b-30 "
                label="  "
                onClick={() => history.push('/result')}
                disabled={totalForPayment <= 0}
              />
              <SVG src={TooltipText} />
            </div>
          </>
        )}
      </div>
    </Layout>
  );
};

export default PaymentPage;
